.blog-content {
	a {
		color: $title-black;
		letter-spacing: 2px;
	}

	h2 {
		margin-bottom: 0px;
		text-align: left;
	}

	p {
		margin-left: 10px;
		font-size: 17px;
	}

	img {
		box-shadow: 0px 3px 12px #767a7b;
		max-width: 100%;
		text-align: left;
		margin-top: 0px;
		margin-bottom: 5px;
		width: 200px;
	}
}

/* Helper Classes ------------------------- */
@media ( max-width: 768px ) {
	.home-blog-title {
		h2 {
			margin-left: 24px;
			margin-right: 23px;
		}
	}

	.blog-content {
		margin-left: 0px;
		margin-right: 0px;
		margin-top: 25px;
		margin-bottom: 20px;

		img {
			margin-bottom: 0px;
			padding-bottom: 0px;
		}

		p{
			padding-bottom: 1px;
		}
	}
}

@media ( max-width: 414px ) {
		.home-blog-title {
		h2 {
			margin-left: 15px;
			margin-right: 14px;
		}
	}
}